import React, { useState, useEffect } from "react";
import { useAdmin } from "../../contexts/AdminContext";
import "./AdminLogin.css";

interface AdminLoginProps {
  onLoginSuccess?: () => void;
}

export const AdminLogin: React.FC<AdminLoginProps> = ({ onLoginSuccess }) => {
  const { loginAdmin, loading, isAdminLoggedIn } = useAdmin();
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState("");
  const [isLoading, setIsLoading] = useState(false);

  // Redirigir si ya está logueado como admin
  useEffect(() => {
    if (isAdminLoggedIn) {
      console.log("🔄 Admin ya logueado, redirigiendo...");
      if (onLoginSuccess) {
        onLoginSuccess();
      } else {
        window.location.href = "/admin-dashboard";
      }
    }
  }, [isAdminLoggedIn, onLoginSuccess]);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError("");
    setIsLoading(true);

    try {
      const result = await loginAdmin(email, password);
      if (result.success) {
        console.log("✅ Login exitoso, redirigiendo...");
        // Redirigir al dashboard
        if (onLoginSuccess) {
          onLoginSuccess();
        } else {
          window.location.href = "/admin-dashboard";
        }
        return; // Salir inmediatamente
      } else {
        setError(result.error || "Error al iniciar sesión");
      }
    } catch (err) {
      setError("Error inesperado");
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="admin-login-container">
      <div className="admin-login-card">
        <div className="admin-login-header">
          <h1>🔐 Admin Panel</h1>
          <p>Acceso de Administrador</p>
        </div>

        <form onSubmit={handleSubmit} className="admin-login-form">
          <div className="admin-form-group">
            <label htmlFor="email">Email</label>
            <input
              type="email"
              id="email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              placeholder="admin@test.com"
              required
              disabled={isLoading}
            />
          </div>

          <div className="admin-form-group">
            <label htmlFor="password">Contraseña</label>
            <input
              type="password"
              id="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              placeholder="••••••"
              required
              disabled={isLoading}
            />
          </div>

          {error && <div className="admin-error">❌ {error}</div>}

          <button
            type="submit"
            className="admin-login-btn"
            disabled={isLoading || loading}
          >
            {isLoading ? "Iniciando sesión..." : "Acceder al Panel"}
          </button>
        </form>

        <div className="admin-login-footer">
          <p>Panel de Administración - Retas de Pádel</p>
        </div>
      </div>
    </div>
  );
};
